@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{margin:0;padding:0;box-sizing:border-box}.App{text-align:center;min-height:100vh;width:100%;display:grid;grid-template-columns:1fr repeat(12, minmax(auto, 7rem)) 1fr;grid-template-rows:repeat(6, 7rem);grid-gap:0 .5rem;gap:0 .5rem;justify-content:center;background:#f3f4fa;font-family:'Quicksand'}.App .header{grid-column:2 / 14;grid-row:1 / 2;display:flex;justify-content:center}.App .header article{padding:.5rem 0}.App .header article h1{font-size:30px;color:#fff;font-weight:500}.App .header article p{color:#5e72e4;background:#fff;padding:.5rem;border-radius:.375rem}.App .summary{grid-column:2 / 5;grid-row:2 / 6}.App .summary .sum__card{background:#fff;border-radius:.375rem;box-shadow:0 0 2rem 0 rgba(136,152,170,0.15);margin-bottom:.5rem;display:flex;justify-content:space-between;padding:.5rem 1rem}.App .summary .sum__card article{display:flex;flex-flow:column wrap;align-items:start;justify-content:space-between}.App .summary .sum__card article p{color:#8898aa;font-size:14px;font-weight:700}.App .summary .sum__card article h4{color:#32325d;font-size:24px}.App .summary .sum__card article .sum__update{display:flex}.App .summary .sum__card article .sum__update img{height:18px}.App .summary .sum__card article .sum__update .update-data{margin-right:1rem;color:#c45850;font-weight:500;font-size:14px}.App .summary .sum__card article .sum__update .update-data.green{color:#16c3bb}.App .summary .sum__card figure .icon{height:80px}.App .line-chart{grid-column:5 / 14;grid-row:2 / 7;background:#fff;border-radius:.375rem}.App .line-chart .chart{height:100%}.App .blue-bg{background:#5e72e4;grid-row:1 / 3;grid-column:1 / 15}@media only screen and (max-width: 700px){.App .summary{grid-column:2 /13;grid-row:2 / 5}.App .line-chart{grid-column:2 /13;grid-row:5 / 7}}.App-logo{height:80px;pointer-events:none;animation:App-logo-spin infinite 20s linear}@keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}

